import { http } from '../utils/http';
import axios from 'axios';

export const getFaq = () => {
    return new Promise((resolve, reject) => {
        axios
            .get(`${process.env.REACT_APP_ROOT_URL_FAQ}/documents-service/faqs-service/faq-categories/faqs`)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
