import React, { useEffect, useState } from 'react';
import Header from '../../home/components/header';
import FooterNew from '../../home/components/FooterNew';
import MainCard from '../../../ui-component/cards/MainCard';
import useStyles from './styles';
import HistoryIcon from '@material-ui/icons/History';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { Paper } from '@material-ui/core';
import { getFaq, getSubFaq } from '../../../services/faqServices';

function Faq() {
    const classes = useStyles();
    const [faqData, setFaqData] = useState([]);

    const GetFaq = async () => {
        await getFaq()
            .then((res) => {
                setFaqData(res.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        GetFaq();
    }, []);

    return (
        <>
            <Header />
            <MainCard style={{ fontWeight: 'lighter', fontFamily: 'Josefin Sans',  margin: '20px', fontSize: '24px' }}>
                <div className={classes.titleContainer}>
                    <HistoryIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp;Faqs </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <div className={classes.root}>
                        {faqData?.map((data) => (
                            <Accordion key={data?.id}>
                                <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1a-content" id="panel1a-header">
                                    <Typography className={classes.heading} style={{ fontWeight: 'lighter', fontSize: '18px', fontFamily: 'Josefin Sans', color: '#301934' }}>
                                        {data?.title}
                                    </Typography>
                                </AccordionSummary>
                                {data?.faqs?.map((subData) => (
                                    <Accordion>
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel1a-content"
                                            id="panel1a-header"
                                        >
                                            <Typography className={classes.heading} style={{ fontWeight: 'lighter', fontSize: '16px', fontFamily: 'Josefin Sans'}}>{subData?.question}</Typography>
                                        </AccordionSummary>

                                        <AccordionDetails>
                                            <Typography style={{ fontWeight: 'lighter', fontSize: '15px', fontFamily: 'Josefin Sans'}}>{'    '+subData?.answer} </Typography>
                                        </AccordionDetails>
                                    </Accordion>
                                ))}
                            </Accordion>
                        ))}
                    </div>
                </Paper>
            </MainCard>
            <FooterNew />
        </>
    );
}

export default Faq;
